import UIService from "./service/ui-service.js";
import { ElementFactory } from "./utils/element-factory";
import { onContextMenu } from "./handlers/contextmenu";
import "./handlers/settingsmenu";
import "./handlers/view-changer-menu";
import "./global.js"
import CRUD from "./service/crud.js";
import { AnchorBuilder, ListItemBuilder, OptionBuilder } from "html-tag-builder";
import { loadBookmarkView, loadCollectionView, loadTodayView } from "./handlers/view-changer-menu";

setAsideSubscriptions();

if (window.location.host.startsWith('collections.')) {
    const search = new URLSearchParams(window.location.search);
    loadCollectionView(search.get('id'));
}
else if (window.location.host.startsWith('bookmarks.'))
    loadBookmarkView();
else loadTodayView();

function setAsideSubscriptions() {
    ElementFactory.main.aside.ul.innerHTML = '';
    ElementFactory.main.aside.searchDataList.innerHTML = '';

    CRUD.get('/subscription')
        .then(subs => {
            subs.forEach(({ channelId, channelTitle, isFavorite }) => {
                ElementFactory.main.aside.ul.append(
                    new ListItemBuilder(
                        new AnchorBuilder(`/channel/${channelId}/${channelTitle}`)
                            .innerText(`${channelTitle} ${isFavorite ? '⭐' : ''}`)
                            .attr('data-channelId', channelId)
                            .title(channelTitle).buildHTML()
                    ).build()
                );

                ElementFactory.main.aside.searchDataList.append(
                    new OptionBuilder(channelTitle, channelTitle).attr('data-channelId', channelId).build()
                );
            })

        })
}

document.querySelector('#query input').addEventListener('keyup', (e) => {
    if (e.key === 'Enter') {
        const { target } = e;
        const value = target.value.trim();
        if (value.length === 0) return;

        const opt = target.list.querySelector(`option[value="${value}"]`);
        if (opt) window.location = `/channel/${opt.getAttribute('data-channelId')}/${value}`;
    }
})

document.querySelectorAll('.btn-group.toggle-group').forEach(e => {[]
    e.addEventListener('click', ({ target }) => {
        if (target.tagName !== 'BUTTON') return;

        e.querySelectorAll('button').forEach(button => button.classList.remove('active'));
        target.classList.add('active');

        if (target.value === 'favorites') {
            UIService.clearAndDisplayLoading(ElementFactory.main.videos.target);
            UIService.loadUnwatchedForToday(ElementFactory.main.videos.target, [0,0], true);
        }
        else if (target.value === 'all') {
            UIService.clearAndDisplayLoading(ElementFactory.main.videos.target);
            UIService.loadUnwatchedForToday(ElementFactory.main.videos.target);
        }
    })
})

ElementFactory.main.aside.ul.addEventListener('contextmenu', onContextMenu, false);