const rf = new Intl.RelativeTimeFormat(
    'en', { numeric: 'auto' }
);

const dtf = new Intl.DateTimeFormat(
    'en-US', {
    day: 'numeric',
    month: 'short',
    year: 'numeric'
});

export default class Formatter {

    static transformViews(views) {
        let resolved = new Intl.NumberFormat('en-US', { maximumSignificantDigits: 4 }).format(views);
        const segments = resolved.split(',');
        const prefix = segments.shift();
        let suffix = '';

        if (segments.length > 0) {
            switch (segments.length) {
                case 1:
                    suffix = 'K'
                    break;
                case 2:
                    suffix = `.${segments[0]}M`;
                    break;
                case 3:
                    suffix = `.${segments[0]}B`;
                    break;
                case 4:
                    suffix = `.${segments[0]}T`;
                    break;
                default:
                    break;
            }
        }

        return prefix + suffix;
    }

    static transformPublishDate(date) {
        const then = new Date(date);
        const now = new Date();
        const days = (now - then) / 86400000;

        if (days < 1) {
            const hours = days * 24;
            if (hours < 1) return rf.format(-Math.round(hours * 60), 'minutes');
            else return rf.format(-Math.round(hours), 'hours');
        }
        else if (Math.round(days) < 8) {
            const format = rf.format(-Math.round(days), 'days');
            return format.charAt(0).toUpperCase() + format.substring(1);
        }
        return dtf.format(then);
    }
}