import { ListItemBuilder } from "html-tag-builder";
import CRUD from "../service/crud";
import { ElementFactory } from "../utils/element-factory";

document.addEventListener('click', e => {
    const cm = document.getElementById('contextmenu-aside');
    if (!cm.classList.contains('active') || cm.contains(e.target)) return;

    cm.classList.remove('active');
    cm.querySelectorAll('ul.menu li:not(:first-child)').forEach(e => e.remove());
});

export function onContextMenu(e) {
    e.preventDefault();
    document.getElementById('contextmenu-aside')
        .querySelectorAll('ul.menu li:not(:first-child)')
        .forEach(e => e.remove());

    let li = e.target.closest('li');
    if (li === null) return;

    document.body.style.cursor = 'wait !important';
    const channelId = li.querySelector('a').getAttribute('data-channelId');

    const menu = document.getElementById('contextmenu-aside');
    menu.style.left = `${e.pageX}px`;
    menu.style.top = `${e.pageY + 10}px`;

    registerCopyYouTubeLink(menu, channelId);
    CRUD.get(`/subscription/${channelId}`)
        .then(channel => {
            registerFavorite(menu.querySelector('ul'), li, channel);
            registerUnsubscribe(menu.querySelector('ul'), li, channel);
            menu.classList.add('active');
            document.body.style.cursor = 'default';
        })
        .catch(console.error)
        .finally(() => {
            document.body.style.cursor = 'default';
            menu.classList.add('active');
        })
}

function registerCopyYouTubeLink(menu, channelId) {
    const copyLI = menu.querySelector('li[value="copyYoutubeLink"]');
    copyLI.classList.remove('loading');
    copyLI.innerHTML = 'Copy YouTube Link';
    copyLI.onclick = ({ target }) => {
        target.classList.add('loading');
        const url = `https://www.youtube.com/channel/${channelId}`;

        if (navigator.permissions) {
            navigator.clipboard.writeText(url).then(function () {
                menu.classList.remove('active');
            }.bind(this), function () {
                target.classList.remove('loading');
            }.bind(this));
        }
        else {
            //fallback
            const ta = document.createElement("textarea");
            ta.value = url;
            document.body.appendChild(ta);
            ta.select();
            document.execCommand('copy');
            document.body.removeChild(ta);
            menu.classList.remove('active');
        }
    }
}

function registerFavorite(menu, li, { channelId, isFavorite }) {
    const favoriteLI = new ListItemBuilder(isFavorite ? 'Unfavorite' : 'Favorite')
        .classes('menu-item')
        .attr('value', 'favorite')
        .on('click', ({ target }) => {
            target.classList.add('loading');

            const updateUIParts = () => {
                target.classList.remove('loading');
                target.innerHTML = isFavorite ? 'Unfavorite' : 'Favorite';

                li.querySelector('a').innerHTML = !isFavorite
                    ? li.querySelector('a').innerHTML.replace('⭐', '')
                    : li.querySelector('a').innerHTML + '⭐';
            }

            CRUD.put(`/subscription/favorite/${channelId}/${+!!!+isFavorite}`, false)
                .then(() => {
                    isFavorite = !!!+isFavorite;
                    target.classList.toggle('text-error');
                })
                .catch(console.error)
                .finally(() => updateUIParts())

        });

    if (isFavorite) favoriteLI.classes('text-error');

    menu.append(favoriteLI.build());
}

function registerUnsubscribe(menu, li, { channelId }) {
    const unsubLI = new ListItemBuilder('Unsubscribe')
        .classes('menu-item', 'text-error')
        .attr('value', 'unsubscribe')
        .on('click', ({ target }) => {
            target.classList.add('loading');

            if (confirm(`Are you sure you want to unsubscribe from ${li.textContent}`)) {
                CRUD.delete(`/subscription/${channelId}`, { method: 'DELETE' })
                    .then(() => {
                        li.remove();
                        document.getElementById('contextmenu-aside').classList.remove('active');

                        const opt = ElementFactory.main.aside.searchDataList.querySelector(`option[data-channelId="${channelId}"]`);
                        if (opt !== null) opt.remove();
                    })
                    .catch((err) => {
                        console.error(err);
                        target.classList.remove('loading');
                    });
            }
            else target.classList.remove('loading');
        })


    menu.append(
        new ListItemBuilder('').classes('divider').attr('value', 'divider').build(),
        unsubLI.build()
    );
}