import { AbstractEnum } from "../model/enum/enum";
import CRUD from "../service/crud";

const PlayerStatus = AbstractEnum.extend({
    UNSTARTED: -1,
    ENDED: 0,
    PLAYING: 1,
    PAUSED: 2,
    BUFFERING: 3,
    CUED: 5
})

export default class YouTubeIframeAPIService {

    static init() {
        const script = document.createElement("script");
        script.type = "text/javascript";
        script.src = "https://www.youtube.com/iframe_api";
        return new Promise(async resolve => {
            const settings = await CRUD.get('/settings');
            if (settings.youtubeIframeAPIEnabled) {
                document.body.appendChild(script);
                window.onYouTubeIframeAPIReady = resolve;
            }
            else resolve();
        });

    }

    static newInstance(elementId) {
        const target = document.getElementById(elementId);
        return new YT.Player(elementId, {
            events: {
                'onReady': e => YouTubeIframeAPIService.onPlayerReady(e, target),
                'onStateChange': e => YouTubeIframeAPIService.onPlayerStateChange(e.data, target)
            }
        })
    }

    static onPlayerReady(event, target) {
        // console.log('player ready');
    }

    static async onPlayerStateChange(playerStatus, target) {
        switch (playerStatus) {
            case PlayerStatus.ENDED:
                const entry = target.closest('.entry').parentElement;
                const settings = await CRUD.get('/settings');
                if (settings.youtubeIframeAPIAutoplay) {
                    const formData = entry.querySelector('yt-image-video').formData;
                    CRUD.post(`/video/${formData.videoId}/watched`, false, formData, 201)
                        .catch(console.error)
                        .finally(async () => {
                            if (entry.nextSibling) {
                                const { nextSibling } = entry;
                                if (document.fullscreenElement)
                                    await document.exitFullscreen();
                                entry.classList.remove('theatre');
                                nextSibling.querySelector('img').click();
                            }
                            else if (document.getElementById('dropdown-video-category').querySelector('button').innerText.startsWith('Collections')) {
                                const first = entry.closest('section').querySelector('.entry');

                                if (document.fullscreenElement)
                                    await document.exitFullscreen();
                                entry.classList.remove('theatre');

                                if (first.querySelector('img') !== null)
                                    first.querySelector('img').click();
                                else first.querySelector('yt-image-video').play();
                            }
                        })
                }
                break;
            default:
                break;
        }
    }

}