import { ListItemBuilder } from "html-tag-builder";
import CRUD from "../../../../service/crud";
import { ElementFactory } from "../../../../utils/element-factory";
import EmptyBuilder from "../../empty";
import ActionsBuilder from "../../actions/actions";
import ActionsService from "../../../../service/actions-service";
import AbstractEntryBuilder from "./entry";

export default class TodayEntryBuilder extends AbstractEntryBuilder {

    constructor(entry) {
        super(entry);

        this.withActions(new ActionsBuilder(entry)
            .withTheatreModeAction()
            .withBookmarkAction()
            .withDropdownItems(
                new ListItemBuilder('Mark as Watched').classes('menu-item')
                    .on('click', ({ target }) => {
                        target.classList.toggle('loading');
                        CRUD.post(`/video/${entry.videoId}/watched`, false, entry, 201)
                            .then(() => {
                                document.querySelector(`[data-videoId="${entry.videoId}"]`).parentElement.remove();
                            })
                            .catch(err => {
                                console.error(err);
                                target.classList.remove('loading');
                            })
                    }),
                new ListItemBuilder('Hide').classes('menu-item')
                    .on('click', () => {
                        if (document.querySelectorAll('.entry').length === 0)
                            ElementFactory.main.videos.target.appendChild(
                                new EmptyBuilder('End of videos', 'loadMore').build()
                            )

                        document.querySelector(`[data-videoId="${entry.videoId}"]`).parentElement.remove();
                    }),
                new ListItemBuilder('').classes('divider'),
                new ListItemBuilder('Add To Collection').classes('menu-item')
                    .on('click', () => ActionsService.handleAddToCollectionAction(this.formData))
            )
        )
    }
}