import { TagBuilder, FigureBuilder,AnchorBuilder, ListBuilder, ListItemBuilder } from "html-tag-builder";
import Formatter from "../../../../utils/formatter";
import YTImageVideo from "../../../custom elements/yt-image-vid";

export default class AbstractEntryBuilder extends TagBuilder {

    constructor(entry) {
        super('div');
        this.classes('entry-container');

        const fd = entry;
        delete fd.entry;

        this.formData = fd;
        this.entry = entry;

        let title = entry.title.substring(0, 80);
        if (entry.title.length > 80) title += ' ...';

        this.append(
            new TagBuilder('div').classes('entry')
                .attr('data-videoId', entry.videoId)
                .attr('data-channelId', entry.channelId)
                .append(
                    new FigureBuilder().append(new YTImageVideo(entry))
                        .margin(0).padding(0),

                    new TagBuilder('div').classes('details').position('relative').append(
                        new TagBuilder('h3').innerText(title).title(entry.title),
                        new TagBuilder('div').classes('byline').append(
                            new AnchorBuilder(`/channel/${entry.channelId}/${entry.author}`).innerText(entry.author)
                        ),
                        new ListBuilder(false, 'none').classes('metadata').append(
                            new ListItemBuilder(Formatter.transformPublishDate(entry.publishedDate))
                        )
                    )
                )
        )
    }

    withActions(actionBuilder) {
        this.actionBuilder = actionBuilder;
        return this;
    }

    build() {
        if (this.actionBuilder)
            this.node.querySelector('div.details').append(this.actionBuilder.build())

        return this.node;
    }

}