import { ListItemBuilder } from "html-tag-builder";
import ActionsService from "../../../../service/actions-service";
import CRUD from "../../../../service/crud";
import ActionsBuilder from "../../actions/actions";
import AbstractEntryBuilder from "./entry";

export default class CollectionEntryBuilder extends AbstractEntryBuilder {
    constructor(entry) {
        super(entry);

        this.withActions(new ActionsBuilder(entry)
            .withTheatreModeAction()
            .withBookmarkAction()
            .withDropdownItems(
                new ListItemBuilder('Add To Collection').classes('menu-item')
                    .on('click', () => ActionsService.handleAddToCollectionAction(this.formData)),
                new ListItemBuilder('').classes('divider'),
                new ListItemBuilder('Delete').classes('menu-item', 'text-error')
                    .on('click', ({ target }) => {
                        target.classList.add('loading');

                        CRUD.delete(`/collection/${entry.collectionId}/association/${entry.videoId}`)
                            .then(() => target.closest('.entry-container').remove())
                            .catch(console.error);
                    })
            )
        )
    }
}