import SelectCollectionModal from "../model/custom elements/modals/select-collection-modal";
import CRUD from "./crud";

export default class ActionsService {

    static handleAddToCollectionAction(entry) {

        CRUD.get(`/collection`, { sortDir : 'asc' })
            .then(collections =>
                document.body.appendChild(new SelectCollectionModal(collections, entry))
            )
            .catch(console.error)

    }

}