import { TagBuilder } from "html-tag-builder";

export default class EmptyBuilder extends TagBuilder {

    constructor(body, id) {
        super('div', id);
        this.classes('empty', 'text-gray');
        this.style({ background: 'none' });

        this.append(
            new TagBuilder('p').classes('empty-title', 'h5').innerHTML(body)
        )
    }

}