import YouTubeIframeAPIService from "./service/youtube-iframe-service";

window.onkeydown = (e) => {
    if (e.key === 'Escape' && document.querySelector('.entry-container.theatre') !== null)
        document.querySelector('.entry-container.theatre').classList.toggle('theatre');
}

window.onclick = (e) => {
    const { target } = e;

    if (target.classList.contains('details') && target.closest('.entry-container').classList.contains('theatre'))
        target.closest('.entry-container').classList.remove('theatre');

    if (!target.classList.contains('theatre') || !target.classList.contains('entry-container')) return;

    target.classList.remove('theatre');
}


YouTubeIframeAPIService.init().then(() => console.log('YouTube Iframe API Ready or resolved because not permitted'));