import { TagBuilder } from "html-tag-builder";

export default class LoadMoreBtnBuilder extends TagBuilder {

    constructor(parent, onClick) {
        super('div', 'loadMore');

        this.classes('grid-col-span-all');

        this.append(
            new TagBuilder('button')
                .classes('btn', 'btn-link')
                .innerText('Load More')
                .on('click', async () => {
                    const div = document.getElementById('loadMore');
                    parent.removeChild(div);
                    parent.append(
                        new TagBuilder('div').classes('empty', 'text-gray', 'grid-col-span-all')
                            .style({
                                'background': 'none'
                            })
                            .append(
                                new TagBuilder('p').classes('empty-title', 'h5').append(
                                    new TagBuilder('button').classes('btn', 'btn-link', 'btn-lg', 'loading')
                                        .attr('disabled', true)
                                )
                            )
                            .build()
                    );
                    parent.scrollTo(0, parent.scrollHeight);
                    await Promise.resolve(onClick());
                })
        )

    }

}