
export default class CRUD {

    static get(path, formData = {}) {
        const query = new URLSearchParams([...Object.entries(formData)]);
        return fetchOnce(`${path}?${query.toString()}`, { expectingStatus: 200 });
    }

    static getPage(path, cursor = [0, 0], limit = 15, formData = {}) {
        const query = new URLSearchParams([
            ['cursor', cursor || [0, 0]],
            ['limit', limit || 15],
            ...Object.entries(formData)
        ]);

        return fetchOnce(`${path}?${query.toString()}`, { expectingStatus: 200 });
    }

    static post(path, isJsonResponse, formData = undefined, expectingStatus = undefined) {
        const query = formData
            ? `?${new URLSearchParams(Object.entries(formData)).toString()}`
            : '';

        return fetchOnce(path + query, { method: 'POST', expectingStatus: expectingStatus || [200, 201], contentType: isJsonResponse ? 'application/json' : 'text/plain' });
    }

    static delete(path) {
        return fetchOnce(path, { method: 'DELETE', expectingStatus: 204, contentType: 'text/plain' });
    }

    static put(path, jsonBody = undefined, expectingStatus = undefined) {
        let contentType = 'text/plain';

        const opts = {
            method: 'PUT',
            expectingStatus: expectingStatus || 200,
            contentType
        }

        if (jsonBody) {
            opts['contentType'] = 'application/json';
            opts['body'] = JSON.stringify(jsonBody);
        }

        return fetchOnce(path, opts);
    }

    static patch(path, expectingStatus = undefined) {
        return fetchOnce(path, { method: 'PATCH', expectingStatus, contentType: 'text/plain' });
    }

}

class CRUDError extends Error {

    constructor(response, body = null) {
        super();
        this.status = response.status;
        this.statusText = response.statusText || null;
        this.body = body || null;
    }

}

const fetchOnce = (path, { method = 'GET', expectingStatus = undefined, contentType = 'application/json', body = undefined }) => {
    return Promise.resolve(fetch(path, {
        method, body, headers: {
        'Content-Type' : contentType
    } })
    .then(async response => {
        if (expectingStatus !== undefined) {
            if (Array.isArray(expectingStatus) && !expectingStatus.includes(response.status))
                throw new CRUDError(response, await response.text());
            else if (expectingStatus !== response.status)
                throw new CRUDError(response, await response.text());

        }
        else if (!expectingStatus && !response.ok) throw new CRUDError(response.status, response.statusText, await response.text());

        return response;
    })
    .then(body => {
        if (contentType.includes('json'))
            return body.json();
        else return body.text();
    })
)

}