import { ListBuilder, TagBuilder } from "html-tag-builder";
import CRUD from "../../../service/crud";

export default class ActionsBuilder extends TagBuilder {

    constructor(entry) {
        super('div');
        this.classes('actions');
        this.entry = entry;

        const fd = entry;
        delete fd.entry;
        this.formData = fd;
    }

    withTheatreModeAction(closestSelector = '.entry-container') {
        this.append(
            new TagBuilder('button')
                .style({ display: 'none' })
                .classes('tooltip', 'tooltip-left', 'theatre', 'btn', 'btn-sm', 'btn-link')
                .attr('data-tooltip', 'Toggle theatre mode')
                .innerHTML(`<svg xmlns="http://www.w3.org/2000/svg" height="20px" viewBox="0 0 24 24" width="20px" fill="#000000" style="margin-top:2px;"><path d="M9 21c0 .55.45 1 1 1h4c.55 0 1-.45 1-1v-1H9v1zm3-19C8.14 2 5 5.14 5 9c0 2.38 1.19 4.47 3 5.74V17c0 .55.45 1 1 1h6c.55 0 1-.45 1-1v-2.26c1.81-1.27 3-3.36 3-5.74 0-3.86-3.14-7-7-7zm2.85 11.1l-.85.6V16h-4v-2.3l-.85-.6C7.8 12.16 7 10.63 7 9c0-2.76 2.24-5 5-5s5 2.24 5 5c0 1.63-.8 3.16-2.15 4.1z" fill="rgb(170,170,170)"/></svg>`)
                .on('click', e => {
                    const entry = e.target.closest(closestSelector);
                    entry.classList.toggle('theatre');
                })
        )
        return this;
    }

    withBookmarkAction() {
        this.append(
            new TagBuilder('button')
                .style({ 'user-select': 'none' })
                .attr('class', this.entry.isBookmarked ? 'bookmark-filled' : '')
                .classes('tooltip', 'tooltip-left', 'btn', 'btn-sm', 'btn-link', 'bookmark')
                .attr('data-tooltip', 'Toggle bookmark')
                .on('click', ({ target }) => {
                    target.classList.add('loading');
                    if (!this.entry.isBookmarked)
                        CRUD.post(`/bookmark/${this.entry.videoId}`, false, this.formData, 201)
                            .then(() => {
                                this.entry.isBookmarked = true;
                                target.classList.remove('loading');
                                target.classList.add('bookmark-filled');
                            })
                            .catch((err) => {
                                console.error(err);
                                target.classList.remove('loading');
                            })
                    else
                        CRUD.delete(`/bookmark/${this.entry.videoId}`)
                            .then(() => {
                                this.entry.isBookmarked = false;
                                target.classList.remove('loading');
                                target.classList.remove('bookmark-filled');
                            })
                            .catch((err) => {
                                console.error(err);
                                target.classList.remove('loading');
                            })
                })
        )
        return this;
    }

    withDropdownItems(...itemBuilders) {
        this.append(
            new TagBuilder('div')
                .classes('dropdown', 'dropdown-right', 'tooltip', 'tooltip-left')
                .attr('data-tooltip', 'More')
                .append(
                    new TagBuilder('a').classes('dropdown-toggle', 'c-hand')
                        .tabIndex(0)
                        .append(
                            new TagBuilder('i')
                                .classes('icon', 'icon-more-vert')
                                .width('18px')
                        ),
                    new ListBuilder().classes('menu').append(
                        ...itemBuilders
                    )
                )
        )
        return this;
    }

}