import { TagBuilder } from "html-tag-builder";
import { ElementFactory } from "../utils/element-factory";
import UIService from "../service/ui-service";
import CollectionListBuilder from "../model/tag-builders/collection-list";
import CollectionEntryBuilder from "../model/tag-builders/components/entries/collection.js";
import CRUD from "../service/crud";
import BookmarkEntryBuilder from "../model/tag-builders/components/entries/bookmark";

const menuOptions = ['bookmarks', 'today', 'collections'];

function reset() {
    ElementFactory.main.videos.target.classList.remove('collection');
    ElementFactory.main.toolbar.todayFilterGroup.style.visibility = 'hidden';
    UIService.clearAndDisplayLoading(ElementFactory.main.videos.target);
}

export function loadCollectionView(id) {
    reset();

    ElementFactory.main.videos.target.classList.add('collection');
    ElementFactory.main.toolbar.dropdownBtn.innerHTML = 'Collections <i class="icon icon-caret"></i>';

    if (id) {
        CRUD.get(`/collection/${id}`)
            .then(e => {
                console.log(e);
                ElementFactory.main.videos.target.innerHTML = '';
                ElementFactory.main.videos.target.append(
                    new TagBuilder('h1').classes('grid-col-span-all').innerHTML(e.name).build()
                )
                ElementFactory.main.videos.target.append(
                    ...e.entries.map(m => new CollectionEntryBuilder(m).build())
                );

            })
            .catch(console.error);
    }
    else
    CRUD.get('/collection')
        .then(collections => {
            ElementFactory.main.videos.target.innerHTML = '';

            if (collections.length === 0)
                UIService.clearAndDisplayNoneFound(ElementFactory.main.videos.target, 'No collections');

            collections.forEach(collection => {
                ElementFactory.main.videos.target.append(
                    new CollectionListBuilder(collection).build()
                )
            })
        })
        .catch(console.error)
}

export function loadTodayView() {
    reset();
    ElementFactory.main.toolbar.todayFilterGroup.style.visibility = 'visible';
    ElementFactory.main.toolbar.dropdownBtn.innerHTML = 'Today <i class="icon icon-caret"></i>';
    UIService.loadUnwatchedForToday(ElementFactory.main.videos.target);
}

export function loadBookmarkView() {
    reset();
    ElementFactory.main.toolbar.dropdownBtn.innerHTML = 'Bookmarks <i class="icon icon-caret"></i>';

    CRUD.get(`/bookmark`)
        .then(bookmarks => {
            UIService.loadPageableEntriesForTarget(
                bookmarks,
                ElementFactory.main.videos.target,
                BookmarkEntryBuilder
            );
        })
        .catch(console.error)
}

ElementFactory.main.toolbar.dropdown.querySelector('.menu').addEventListener('click', ({ target }) => {
    if (target.tagName === 'LI' && target.classList.contains('menu-item')) {
        const menuItem = target.getAttribute('value');

        if (!menuOptions.includes(menuItem)) return;

        if (menuItem === 'bookmarks') window.location = `${window.location.protocol}//bookmarks.localhost:${window.location.port}`;
        else if (menuItem === 'today') window.location = `${window.location.protocol}//localhost:${window.location.port}`;
        else if (menuItem === 'collections') window.location = `${window.location.protocol}//collections.localhost:${window.location.port}`;
    }
})