import { ElementFactory } from "../utils/element-factory";
import CRUD from "../service/crud";
import SettingsModal from "../model/custom elements/modals/settings-modal";
import ExportModal from "../model/custom elements/modals/export-modal";
import AddCollectionModal from "../model/custom elements/modals/add-collection-modal";
import AddSubscriptionModal from "../model/custom elements/modals/add-subscription-modal";

ElementFactory.main.menu.ul.addEventListener('click', async ({ target }) => {
    if (target.tagName === 'LI' && target.classList.contains('menu-item')) {
        const menuItem = target.textContent.toLowerCase().trim();
        if (menuItem === 'settings') {
            const settings = await CRUD.get("/settings");
            document.body.append(new SettingsModal(settings));
        }
        else if (menuItem === 'add subscription') {
            document.body.append(new AddSubscriptionModal());
            document.body.querySelector('add-subscription-modal input').focus();
        }
        else if (menuItem === 'add collection') {
            document.body.append(new AddCollectionModal());
            document.body.querySelector('add-collection-modal input').focus();
        }
        else if (menuItem === 'export') {
            document.body.append(new ExportModal());
        }
    }
})