import EmptyBuilder from "../model/tag-builders/empty";
import LoadMoreBtnBuilder from "../model/tag-builders/loadmore";
import CRUD from "../service/crud";
import TodayEntryBuilder from "../model/tag-builders/components/entries/today.js";

export default class UIService {

    static clearAndDisplayLoading(target) {
        target.innerHTML = '';
        target.append(
            new EmptyBuilder(`<button class="btn btn-link btn-lg loading" disabled></button>`).build()
        )
    }

    static clearAndDisplayNoneFound(target, body) {
        target.innerHTML = '';
        target.append(new EmptyBuilder(body || `No videos found`).build());
    }

    static loadPageableEntriesForTarget(entries, target, entryBuilderClass, page = 0) {
        UIService.clearAndDisplayLoading(target);

        const loadEntries = (entries, page = 0) => {
            entries = entries.slice(page, page + 15);

            if (entries.length === 0 && page === 0) {
                UIService.clearAndDisplayNoneFound(target);
                return;
            }
            else if (entries.length === 0) {
                const loadMoreBtn = document.getElementById('loadMoreBtn');
                if (loadMoreBtn) target.removeChild(loadMoreBtn);
            }
            else {
                if (page === 0) { target.innerHTML = ''; }
                entries.forEach(entry => {
                    target.append(Reflect.construct(entryBuilderClass, [entry]).build())
                })
                if (entries.slice(page + 15).length > 0) {
                    target.append(
                        new LoadMoreBtnBuilder(target, () => {
                            loadEntries(entries, page + 1);
                        })
                    )
                }
            }

        }

        loadEntries(entries, page);
    }

    static loadUnwatchedForToday(target, cursor = [0, 0], isFavoritesOnly = false) {
        const section = target;
        UIService.clearAndDisplayLoading(section);

        const fetchUnwatchedEntriesForTodayRange = async (cursor) => {
            const settings = await CRUD.get("/settings");
            CRUD.getPage(`/subscription/today/${isFavoritesOnly}`, cursor, 15, { dayRange: settings.newVideoDateRange })
                .then(response => {
                    const loadMore = document.getElementById('loadMore');
                    if (loadMore)
                        section.remove(loadMore);

                    if (response.results.length === 0 && cursor[0] === 0)
                        UIService.clearAndDisplayNoneFound(section);
                    else {
                        document.querySelectorAll('div.empty').forEach(e => e.parentElement.removeChild(e));
                        const results = response.results.sort((a, b) => new Date(b.publishedDate) - new Date(a.publishedDate));
                        section.append(
                            ...results.map(e => new TodayEntryBuilder(e).build())
                        )
                    }

                    if (!response.isComplete) {
                        section.append(
                            new LoadMoreBtnBuilder(section, () => {
                                fetchUnwatchedEntriesForTodayRange(response.cursor);
                            }).build()
                        )
                    }
                })
                .catch((err) => {
                    console.error('Frontend error fetching today', err);
                    document.querySelector('div.empty').remove();
                    if (cursor[0] === 0)
                        UIService.clearAndDisplayNoneFound(section, 'Error fetching entries');
                })
        }

        fetchUnwatchedEntriesForTodayRange(cursor);
    }

}