import { ListItemBuilder, ListBuilder, TagBuilder, ImageBuilder } from "html-tag-builder";
import CRUD from "../../service/crud";
import AddVideoToCollectionModal from "../custom elements/modals/add-video-to-collection-modal";
import ActionsBuilder from "./actions/actions";

export default class CollectionListBuilder extends TagBuilder {

    constructor(collection) {
        super('div');

        this.classes('entry', 'collection');

        const thumbnailDiv = new TagBuilder('div').classes('thumbnails')
            .on('click', () =>
                window.location = `${window.location.origin}?id=${collection.id}`
            );

        collection.videos.forEach(videoId => {
            thumbnailDiv.append(
                new ImageBuilder(`https://i4.ytimg.com/vi/${videoId}/maxresdefault.jpg`, 'collection video thumbnail')
            )
        })

        this.append(
            thumbnailDiv,
            new TagBuilder('div').classes('details').position('relative').append(
                new TagBuilder('h3').innerText(collection.name).title(collection.name),
                new ListBuilder(false, 'none').classes('metadata').append(
                    new ListItemBuilder(`${collection.totalVideos} videos`)
                ),
                new ActionsBuilder(collection).origin('18%').withDropdownItems(
                    new ListItemBuilder('Add Video').classes('menu-item')
                        .on('click', () => {
                            document.body.appendChild(new AddVideoToCollectionModal(collection.id));
                            document.querySelector('add-video-to-collection-modal input').focus();
                        }),
                    new ListItemBuilder('').classes('divider'),
                    new ListItemBuilder('Delete').classes('menu-item', 'text-error')
                        .on('click', ({ target }) => {
                            target.classList.add('loading');
                            CRUD.delete(`/collection/${collection.id}`)
                                .then(() => target.closest('.entry.collection').remove())
                                .catch(console.error);
                        })
                )
            )
        )
    }

}
